@font-face {
    font-family: "w95";
    font-style: normal;
    font-weight: normal;
    src: local("w95"), local("w952"), url("fonts/w95fa.woff") format("woff"), url("fonts/w95fa.woff2") format("woff2");
}
  
html, body {
    width: 100%;
    height: 100%;
    min-width: 375px;
    min-height: 600px;
}
  
body {
    margin: 0;
    font-family: "w95", monospace, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    font-family: "w95", monospace, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
  
#root {
    width: 100%;
    height: 100%;
}